body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fefefe;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar-character {
    border: 1px solid black;
    position: relative;
    box-sizing: border-box;
    height: 156px;
}

.sidebar-character .name-block {
    font-size: 17px;
}

@media only screen and (max-width: 750px) {
    .sidebar-container {
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
        margin: 20px 0;
    }
}
.container {
    width: 65px;
    height: 65px;
    border: 1px solid black;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;
}

.selected {
    background-color: #1ff859;
}

.banned {
    background-color: #dc3545;
}

.name-block {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: #000000aa;
    color: white;
    font-size: 10px;
}

.banned-foreground {
    width: 100%;
    height: 100%;
    position: absolute;
}

.banned-block {
    width: 65px;
    height: 10px;
    background-color: black;
    position: absolute;
    top: 25px;
}

.left {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.right {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.option-container label,
.option-container input {
    cursor: pointer;
}

.option-container input {
    margin-right: 10px;
}
.character-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: calc(65px * 6);
}

.generate {
    height: 25px;
    background-color: #3f51b5;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
}

.action-bar {
    width: calc(65px * 6);
    margin: 0 auto;
    height: 40px;
    display: flex;
    align-items: center;
}

.main-screen {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}


@media only screen and (max-width: 750px) {
    .main-screen {
        padding-top: 60px;
    }

    .action-bar { 
        padding: 10px 0;
        position: fixed;
        z-index: 2;
        background: #efefef;
        width: 100%;
        border-bottom: 1px solid black;
        flex-direction: row-reverse;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    .generate {
        height: 40px;
        margin-right: 15px;
    }
}
.App {
  text-align: center;
}

